.product-item{
    width: 320px;
}

.product-item__card{
    width: 320px;
    min-height: 509px;
    border: 4px solid var(--blue-text);
    border-radius: 12px;
    clip-path: polygon(15% 0%, 100% 0, 100% 0, 100% 85%, 100% 100%, 0 100%, 0% 85%, 0 8%);
    position: relative;
    padding: 21px 51px 0 47px;
    &::before {
        content: "";
        position: absolute;
        top: 16px;
        left: -11px;
        width: 64px;
        height: 4px;
        background-color: var(--blue-text);
        transform: rotate(-40deg);
    }

    &:hover{
        border-color: var(--blue-hover);

        &::before{
            background-color: var(--blue-hover);
        }
    }
}

.product-item__description{
    color: var(--description-text);
    font-size: 16px;
    line-height: 18.5px;
}
.product-item__title{
    font-size: 24px;
    line-height: 28px;
    margin-top: 5px;
    margin-bottom: 15px;
}
.product-item__title_size_size{
    font-size: 48px;
    line-height: 56px;
    font-weight: 700;
}
.product-item__quantity{
    color: var(--description-text);
    font-size: 14px;
    line-height: 16px;
}
.product-item__quantity_weight_weight{
    font-weight: 700;
}

.product-item__cat{
    position: absolute;
    bottom: -90px;
    left: -38px;
    overflow: hidden;
}
.product-item__cat-image{
    width: 368px;
    height: 360px;
}
.product-item__weight{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: var(--blue-text);
    color: var(--title-text);
    font-size: 21px;
    line-height: 22px;
    bottom: 12px;
    right: 12px;
    z-index: 2;
}
.product-item__weight-size{
    font-size: 42px;
    line-height: 42px;
}
.product-item__question{
    font-size: 13px;
    line-height: 15px;
    color: var(--title-text);
    text-align: center;
}
.product-item__buy{
    color: var(--blue-text);
    font-weight: 700;
    text-decoration: dashed;
    &:hover{
        color: var(--blue-hover);
    }
}
.product-item__card:hover .product-item__weight{
    background-color: var(--blue-hover);
}